body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Button {
  display: inline-block;
  padding: 4px 15px;
  border-radius: 3px;
  background-color: #6b5b95;
  cursor: pointer;
  -webkit-transition: 150ms -webkit-filter linear;
  transition: 150ms -webkit-filter linear;
  transition: 150ms filter linear;
  transition: 150ms filter linear, 150ms -webkit-filter linear;
  font-size: 13px;
}

.Button:hover {
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
  -webkit-transition: 100ms -webkit-filter linear;
  transition: 100ms -webkit-filter linear;
  transition: 100ms filter linear;
  transition: 100ms filter linear, 100ms -webkit-filter linear;
}

.Button.disabled {
  background-color: #666;
  cursor: default;
}

.TabbedSection {
  font-size: 14px;
}

.TabbedSection .tabbedHeader {
  background-color: rgba(0,0,0,0.4);
  padding: 5px 20px;
  margin: 10px 0;
  cursor: pointer;
}

.TabbedSection .tabbedHeader span:last-child {
  float: right;
}

.ModelMenu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Sidebar .ModelMenu .menuHeader {
  background-color: #587291;
}

.modelProperties {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 20px;
}

.modelProperties > div, .dimensionsField > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 13px;
}

.modelProperties > div:last-child {
  margin-top: 20px;
}

.modelInput {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 20px;
}

.modelInput input {
  width: 200px;
}

.dimensionsField {
  margin-top: 10px;
  border-radius: 5px;
  font-size: 13px;
}

.dimensionsField legend {
  font-size: 13px;
}


.OperationList {
  margin: 20px;
}

.OperationList .listContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-self: stretch;
          align-self: stretch;
  height: 200px;
  background-color: rgba(200,200,200,0.6);
  border-radius: 3px;
  margin-bottom: 10px;
}

.OperationList .listContainer .operation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 4px 8px;
  font-size: 13px;
  cursor: pointer;
}

.OperationList .listContainer .operation:hover, .OperationList .listContainer .operation.selected {
  background-color: rgba(0,0,0,0.2);
}

.OperationList .listContainer .operation .opName::first-letter {
  text-transform: uppercase;
}

.operationList .listContent {

}

.operationList .buttonContainer {

}



.ToolField {
  border-radius: 5px;
  margin: 10px;
}

.ToolField .propertyField {
  padding: 0 !important;
}

.OperationProperties {

}

.OperationProperties .message {
  margin-top: 20px;
  text-align: center;
  font-size: 13px;
}

.propertyField {
  font-size: 13px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 25px;
  padding: 0 20px;
}

.propertyField > span:last-child {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}
.propertyField select {
  width: 82px;
  height: 20px;
  text-align: center;
  border: 0;
  margin: 0;
  padding: 0;
  background-color: #fff;
}

.propertyField select option{
  text-align: center;
}

.propertyField input {
  width: 50px;
  height: 20px;
  text-align: center;
  border: 0;
  margin: 0;
  padding: 0;
  border-radius: 3px 0 0 3px;
}

.propertyField .propSuffix {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 20px;
  width: 30px;
  background-color: #fff;
  color: #333;
  border-radius: 0 3px 3px 0;
  border-left: 1px solid #bbb;
  font-size: 12px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.OperationProperties .buttonContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 20px;
}

.Sidebar .OperationsMenu .menuHeader {
  background-color: #2F97C1;
}


.Sidebar .SimulateMenu .menuHeader {
  background-color: #1CCAD8;
}

.Sidebar .ExportMenu .menuHeader {
  background-color: #B4656F;
}


.Sidebar {
  font-family: Verdana, Geneva, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  position: absolute;
  top: 0px;
  left: 0px;
  background: rgba(34,34,34,0.75);
  height: 100%;
  width: 350px;
  margin: 0;
  padding: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.Sidebar .headers {
  width: 80px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Sidebar .headers > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 10px;
  padding: 10px 10px;
  -webkit-transition: 150ms -webkit-filter linear;
  transition: 150ms -webkit-filter linear;
  transition: 150ms filter linear;
  transition: 150ms filter linear, 150ms -webkit-filter linear;
}

.Sidebar .headers > div:hover {
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
  cursor: pointer;
  -webkit-transition: 150ms -webkit-filter linear;
  transition: 150ms -webkit-filter linear;
  transition: 150ms filter linear;
  transition: 150ms filter linear, 150ms -webkit-filter linear;
}

.Sidebar .headers > div > img {
  height: 30px;
  padding-bottom: 5px;
}

.Sidebar .SideMenu, .Sidebar .menuItem {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background: rgba(34,34,34,0.5);
}

.Sidebar .SideMenu .menuHeader {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 4px 10px;
}

.Sidebar .SideMenu .menuHeader img {
  height: 40px;
  padding-right: 10px;
}

.PendingOperations {
  position: absolute;
  bottom: 30px;
  background-color: rgba(10,10,10,0.75);
  color: #fff;
  padding: 0px 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 260px;
}

.PendingOperations.active {
  right: 30px;
  -webkit-animation: fadeIn 0.3s;
          animation: fadeIn 0.3s;
}

.PendingOperations.inactive {
  right: -300px;
  -webkit-animation: fadeOut 0.3s;
          animation: fadeOut 0.3s;
}

.pendingOp {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 10px 0px;
}

.pendingOp div:first-child {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.pendingOp div:first-child span:nth-child(2) {
  text-align: left;
  font-size: 12px;
  color: #eee;
  text-decoration: underline;
  cursor: pointer;
}

.loadingSpinner {
  display: inline-block;
  width: 35px;
  height: 35px;
}

.loadingSpinner:after {
  content: " ";
  display: block;
  width: 25px;
  height: 25px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: loadingSpinner 1.2s linear infinite;
          animation: loadingSpinner 1.2s linear infinite;
}

@-webkit-keyframes loadingSpinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loadingSpinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fadeIn {
  from { right: -300px; } 
  to { right: 30px; } 
}

@keyframes fadeIn {
  from { right: -300px; } 
  to { right: 30px; } 
}

@-webkit-keyframes fadeOut {
  from { right: 30px; } 
  to { right: -300px; } 
}

@keyframes fadeOut {
  from { right: 30px; } 
  to { right: -300px; } 
}

.WebglDisplay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -99;
}

html, body {
  min-height: 100%;
  width: 100%;
  overflow: hidden;
}

.App {
  width: 100%;
  height: 100%;
}


